<template>
  <li :key="skill.id" class="row">
      <div class="col-3">
          {{ skill.name }}
      </div>
      <div class="col align-self-center">
          <div class="progress-bar">
            <span class="bar">
                <span class="progress" :style="{width: setWidth}"></span>
            </span>
        </div>
      </div>
      <div class="col-3">
          <small>{{skill.used}}</small>
      </div>
    
    
  </li>
</template>

<script>
export default {
  props: ["skill"],
  computed: {
      setWidth: function () {
          if (this.skill.rate === "ADVANCE") {
            return "80%"
          } else if (this.skill.rate === "BASIC") {
            return "60%"
          } else if (this.skill.rate === "GOOD") {
            return "50%"
          } else if (this.skill.rate === "MEDIUM") {
            return "30%"
          }
      }
  }
};
</script>

<style>

li {
  display: block;
}

.progress-bar {
	border-radius: 20px;
	overflow: hidden;
    width: 100%;
}
.progress-bar span {
    display: block;
}

.bar {
  background: rgba(0,0,0,0.075);
}

.progress {
  background: #EC9770;
  color: #fff;
  padding: 5px;
  width: 0;
  border-radius: 20px;
}

.progress-bar {
  max-width: 100%;
  display: block;
}
</style>
